/* =====================================================================================
Variables */
body {
  font-family: "Ubuntu", sans-serif;
  font-weight: 300; }

.header__inner {
  font-family: "Ubuntu", sans-serif;
  font-weight: 500; }

.header__info-phone {
  font-family: "Ubuntu", sans-serif;
  font-weight: 700; }

.page-section__title {
  font-family: "SFProDisplay-Regular", sans-serif;
  font-weight: 400; }

.hero__title {
  font-family: "SFProDisplay-Semibold", sans-serif;
  font-weight: 600; }

/* =====================================================================================
Basic styles */
body {
  position: relative;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-size: 17px;
  line-height: 26px;
  color: #fff;
  scroll-behavior: smooth; }

*,
*::before,
*::after {
  box-sizing: inherit; }

a {
  text-decoration: none;
  color: inherit; }

img {
  max-width: 100%;
  height: auto; }

/* =====================================================================================
Container */
.container {
  max-width: 1270px;
  width: 100%;
  margin: 0 auto;
  padding-top: 0;
  padding-right: 15px;
  padding-bottom: 0;
  padding-left: 15px; }

/* =====================================================================================
Media max-width: 1150px*/
/* =====================================================================================
Media max-width: 1150px*/
/* =====================================================================================
Visually-hidden */
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

/* =====================================================================================
Page-section */
.page-section {
  position: relative;
  background-color: #454c44;
  background-image: url(../img/page/page-bg@1x.jpg);
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover; }

.page-section::before {
  content: "";
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  background: linear-gradient(119.35deg, rgba(23, 26, 28, 0.72) 0%, #5e5e57 100%);
  opacity: 0.15; }

.page-section__wrapper {
  position: relative;
  z-index: 1;
  min-height: 100vh;
  padding-top: 74px;
  padding-bottom: 50px; }

.page-section__title {
  margin-bottom: 55px;
  padding-left: 25px;
  font-size: 44px;
  line-height: 53px; }

/* =====================================================================================
Scroll */
.scroll {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  padding-top: 13px;
  padding-bottom: 30px;
  text-align: center; }

/* =====================================================================================
Header */
.header {
  position: fixed;
  top: 30px;
  right: 0;
  left: 0;
  z-index: 10; }

.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 30px;
  font-size: 15px;
  line-height: 17px;
  text-transform: uppercase; }

.header__logo img {
  height: 27px;
  width: 185px; }

.header__info-btn {
  margin-right: 8px;
  padding-top: 6px;
  padding-right: 28px;
  padding-bottom: 6px;
  padding-left: 28px;
  color: #ffffff;
  text-transform: uppercase;
  background-color: transparent;
  border: 1px solid #ffffff;
  border-radius: 3px;
  cursor: pointer;
  transition: all linear 0.2s; }

.header__info-btn:focus,
.header__info-btn:hover {
  color: #000000;
  background-color: #ffffff; }

.header__info-btn:active {
  color: #000000;
  background-color: rgba(255, 255, 255, 0.7); }

.header__info-phone {
  font-size: 16px;
  line-height: 18px; }

/* =====================================================================================
Menu */
.menu__list {
  display: flex;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none; }

.menu__list-item + .menu__list-item {
  padding-left: 20px; }

/* =====================================================================================
Hero */
.hero {
  background-image: url(../img/hero/hero-bg@1x.jpg); }

.hero::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: radial-gradient(45.97% 54.43% at 50% 45.57%, rgba(23, 26, 28, 0) 50.28%, rgba(23, 26, 28, 0.00225974) 50.29%, rgba(4, 5, 5, 0.820343) 58.72%, rgba(5, 5, 6, 0.792703) 80.08%, #000000 87.89%);
  opacity: 0.4; }

.hero__wrapper {
  display: flex;
  align-items: center; }

.hero__inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center; }

.hero__title {
  margin-top: 0;
  margin-bottom: 25px;
  font-size: 96px;
  line-height: 115px; }

.hero__img {
  height: 199px;
  width: 660px; }

.hero__text {
  max-width: 325px;
  margin-top: 25px;
  margin-bottom: 35px; }

.herro__social-list {
  display: flex;
  justify-content: space-between;
  max-width: 220px;
  width: 100%;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none; }

.herro__social-link {
  opacity: 0.5;
  transition: all linear 0.2s; }

.herro__social-link:focus,
.herro__social-link:hover {
  opacity: 0.9; }

.herro__social-link:active {
  opacity: 1; }
