@font-face {
  font-family: "SFProDisplay-Light", sans-serif;

  src: local("Oswald"), url("../fonts/SFProDisplay-Light.woff2") format("woff2"),
    url("../fonts/SFProDisplay-Light.woff") format("woff");

  font-weight: 300;
  font-style: normal;
  font-display: swap;
}

